import { useEffect, useState } from "react";

/**
 * @description manages the dayzed date property for the calendar
 * @param date
 */
const useDateInView = (date?: Date) => {
	const [dateInView, setDateInView] = useState<Date | undefined>(date);

	useEffect(() => {
		setDateInView(date);
	}, [date]);

	return {
		dateInView,
		setDateInView,
	};
};

export default useDateInView;
